import axios from 'axios'

const instance = axios.create({
    baseURL:'http://localhost/laravel-moneytransfers/api/'
});

export default instance

export const axiosGet = (url,actions,dispatch)=>instance
                                .get(url)
                                .then(response=>{  
                                  actions.map(action=>{
                                    action.result = response.data
                                      dispatch(action)
                                  })
                                })

export const axiosPost = (url,data,action,dispatch)=>instance
                                .post(url,data)
                                .then(response=>{
                                    action.result = response.data
                                    dispatch(action)
                                })
                                .catch(err=>console.log('error found',err))


