import * as actionTypes from '../actions/actionTypes'
const initialState = {
    banks:[],
    pickup_banks:[],
    account_banks:[],
    bank:null
}

const reducer = (state = initialState,action)=>{

    switch(action.type){

        case actionTypes.ADD_BANK:
            return{
                ...state,
                bank:action.result
            }
        case actionTypes.FETCH_BANKS:
            return{
                ...state,
                banks:action.result
            }

         case actionTypes.FETCH_ACCOUNT_BANKS:
            const account = state.banks.filter(bank=>bank.status === 'b')
            return{
                ...state,
                account_banks:account
            }
            case actionTypes.FETCH_PICKUP_BANKS:
                const pickup = state.banks.filter(bank=>bank.status === 'p')
            return{
                ...state,
                pickup_banks:pickup
            }
        default:
            return state

    }
}

export default reducer


